@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", verdana, sans-serif;
  font-size: 1em;
  line-height: 1em;
  font-weight: normal;
  text-decoration: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

ul,
ol {
  list-style: none; }

button,
input {
  background: transparent;
  border: none;
  outline: none;
  box-shadow: none;
  cursor: pointer; }

.bttn {
  padding: 15px 30px;
  font-size: 1.2em;
  font-weight: 500;
  color: #404751;
  border: 1px solid #CFD1D3;
  cursor: pointer; }
  .bttn:hover {
    background-color: #CFD1D3; }

.inputField {
  padding: 20px 40px; }
  .inputField label {
    display: block;
    margin-bottom: 10px;
    font-size: 1.2em;
    font-weight: 700;
    color: #404751; }
  .inputField input {
    width: 100%;
    padding: 10px 20px;
    font-size: 1em;
    color: #404751;
    border: 1px solid #CFD1D3; }
  .inputField button {
    padding: 15px 30px;
    font-size: 1.2em;
    font-weight: 700;
    color: #404751;
    border: 1px solid #CFD1D3;
    cursor: pointer; }
    .inputField button:hover {
      background-color: #CFD1D3; }

.Component {
  width: 100%;
  height: 100%; }
  .Component main {
    padding: 40px; }

.Login {
  width: 100%;
  height: 100%;
  padding: 40px 20px; }
  .Login header {
    padding: 40px 0; }
    .Login header h1 {
      font-size: 2em;
      font-weight: 700;
      color: #404751;
      text-align: center; }
  .Login main {
    padding: 20px 0; }

.Game .question {
  padding-bottom: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  border-bottom: 2px solid #CFD1D3; }
  .Game .question h1 {
    font-size: 3em;
    font-weight: 500; }
    .Game .question h1 img {
      width: 8vw;
      height: auto; }
  .Game .question h2 {
    padding-bottom: 20px;
    font-size: 1.6em;
    font-style: italic; }

.Game .teams {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 40px; }
  .Game .teams .team {
    max-width: 350px;
    max-height: 200px;
    margin: 0 20px 20px 0;
    border: 1px solid #CFD1D3;
    font-size: 25px; }
    .Game .teams .team.correct {
      background-color: #f6fadc; }
    .Game .teams .team.incorrect {
      background-color: #ffcccc; }
    .Game .teams .team.correct .Game .teams .team.incorrect {
      border: 1px solid #b4b8bb; }
      .Game .teams .team.correct .Game .teams .team.incorrect button[class^='itemButton'] {
        border: 1px solid #b4b8bb; }
      .Game .teams .team.correct .Game .teams .team.incorrect .AnswerItem__correct {
        display: none; }

.Item {
  padding: 20px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  overflow: hidden;
  border-bottom: 1px solid #CFD1D3; }
  .Item.current {
    background-color: lightblue; }
  .Item.greyedOut {
    background-color: grey; }
  .Item .itemIndex {
    padding-right: 10px; }
  .Item .itemTextHolder {
    overflow: hidden;
    text-overflow: ellipsis; }
    .Item .itemTextHolder .itemTeam {
      margin: 5px 0;
      display: block;
      font-size: 0.8em;
      font-weight: 700; }
  .Item .itemButtonsHolder {
    margin-left: auto; }
  .Item .itemButtonHolder {
    padding-left: 10px; }
  .Item span {
    line-height: 1em;
    color: #404751; }
  .Item button[class^="itemButton"] {
    width: 2em;
    height: 2em;
    margin-left: auto;
    border-radius: 100%;
    background-color: transparent;
    border: 1px solid #CFD1D3;
    cursor: pointer; }
    .Item button[class^="itemButton"] svg {
      width: 33%;
      height: 33%;
      fill: #404751; }
    .Item button[class^="itemButton"][class$="__close"]:hover {
      background-color: #FF0000;
      border: none; }
      .Item button[class^="itemButton"][class$="__close"]:hover svg {
        fill: #fff; }
    .Item button[class^="itemButton"][class$="__accept"]:hover {
      background-color: #c6e327;
      border: none; }
      .Item button[class^="itemButton"][class$="__accept"]:hover svg {
        fill: #fff; }

nav {
  width: 100%;
  height: 60px;
  padding: 5px 20px;
  display: -webkit-flex;
  display: flex;
  position: relative;
  background-color: #404751;
  font-size: 20px; }
  nav .navInner {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    line-height: 50px;
    position: relative; }
    nav .navInner .navLeft {
      position: absolute;
      top: 0;
      left: 0; }
      nav .navInner .navLeft button {
        color: #fff; }
        nav .navInner .navLeft button:hover {
          color: #e800e9; }
    nav .navInner .navMiddle {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-flow: column nowrap;
              flex-flow: column nowrap;
      -webkit-align-items: center;
              align-items: center;
      margin: auto; }
      nav .navInner .navMiddle h2 {
        color: #fff; }
    nav .navInner .navRight {
      position: absolute;
      top: 0;
      right: 0; }
    nav .navInner > div {
      height: 100%;
      display: -webkit-flex;
      display: flex; }
      nav .navInner > div h1,
      nav .navInner > div h2,
      nav .navInner > div span {
        margin: auto 0;
        font-size: 0.9em;
        color: #fff; }

.EndRound main {
  width: 100%;
  max-width: 800px;
  margin: 0 auto; }
  .EndRound main .endRoundMsg {
    font-size: 5vw;
    text-align: center; }

.EndRound ul {
