    padding: 15px 30px;
    border: 1px solid #CFD1D3;
    display: -webkit-flex;
    display: flex; }
    .EndRound ul li.disabled {
      cursor: not-allowed;
      background-color: #CFD1D3; }
    .EndRound ul li .position {
      padding-right: 20px; }
    .EndRound ul li .score {
      margin-left: auto; }

.EndRound h1 {
  padding-bottom: 20px; }

